import React, { Component } from 'react';
import { getBuildingRooms } from 'services/buildingRoom';
import to from 'await-to-js';
import { Divider, Table } from 'antd';

class TableRoomsForAssignSubject extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      currentRecord: {},
    };
  }

  // componentWillMount = () => {
  //   this.getBuildingRoom();
  // };
  componentWillReceiveProps(nextProps) {
    if (nextProps.dataSearch !== this.props.dataSearch) {
      this.getBuildingRoom(nextProps.dataSearch);
    }
  }

  onClickOnRowTable = record => {
    this.props.chooseRoom(record);
    this.setState({
      currentRecord: record,
    });
  };

  getBuildingRoom = async (value = {}) => {
    this.setState({ loading: true });
    const [error, response] = await to(getBuildingRooms(value));
    this.setState({ loading: false });
    if (error) return;
    this.setState({ data: response.data.items });
  };

  setRowClassName = record => {
    const { currentRecord } = this.state;

    if (record.id === currentRecord.id) {
      return 'row-group-type';
    }
    return '';
  };

  expandedRoomRowRender = floor => {
    const innerColumns = [
      {
        title: 'Ký hiệu phòng',
        dataIndex: 'label',
        key: 'label',
        className: 'column-room-label',
      },
      {
        title: 'Sức chứa',
        dataIndex: 'size',
        key: 'size',
      },
      {
        title: 'Loại phòng',
        dataIndex: 'roomType',
        key: 'roomType',
      },
      {
        title: 'Thiết bị',
        dataIndex: 'devices',
        key: 'devices',
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={floor.roomList || []}
        pagination={false}
        // rowSelection
        onRow={record => ({ onClick: () => this.onClickOnRowTable(record) })}
        className="room-table"
        rowKey="id"
        rowClassName={this.setRowClassName}
      />
    );
  };

  expandedFloorRowRender = buildingBase => {
    const innerColumns = [
      {
        title: 'Tầng',
        dataIndex: 'buildingFloor',
        key: 'buildingFloor',
        className: 'building-floor-column',
      },
      { title: 'SL phòng', dataIndex: 'noOfRoom', key: 'noOfRoom' },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={buildingBase.floorList || []}
        pagination={false}
        expandedRowRender={this.expandedRoomRowRender}
        className="floor-table"
        rowKey="buildingFloor"
      />
    );
  };
  render() {
    const outerColumns = [
      {
        title: 'Cơ sở đào tạo',
        dataIndex: 'buildingBase',
        key: 'buildingBase',
        className: 'building-base',
      },
      { title: 'Tòa nhà', dataIndex: 'building', key: 'building' },
      { title: 'SL tầng', dataIndex: 'noOfFloor', key: 'noOfFloor' },
    ];
    return (
      <div>
        <Divider>Danh sách phòng học</Divider>
        <Table
          loading={this.state.loading}
          className="building-table"
          columns={outerColumns}
          expandedRowRender={this.expandedFloorRowRender}
          dataSource={this.state.data || []}
          rowKey="building"
          bordered
        />
      </div>
    );
  }
}

export default TableRoomsForAssignSubject;



