import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import to from 'await-to-js';
import { BtnSearch } from 'components/common/button';
import { getBuildingBases } from 'services/building';
import { getRoomTypes } from 'services/roomType';

import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {
    listBuildings: [],
    listRoom: [],
  };

  componentDidMount() {
    this.getListBuildingBases();
    this.getListRoom();
  }

  getListBuildingBases = async () => {
    const [error, response] = await to(getBuildingBases());
    if (error) return;
    this.setState({
      listBuildings: response.data.items,
    });
  };

  getListRoom = async () => {
    const [error, response] = await to(getRoomTypes({ label: '' }));
    if (error) return;
    this.setState({
      listRoom: response.data.items,
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.formData(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { listBuildings, listRoom } = this.state;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Cơ sở:">
              {getFieldDecorator(
                'buildingBaseId',
                {}
              )(
                <Select>
                  {listBuildings.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loại phòng:">
              {getFieldDecorator(
                'roomTypeId',
                {}
              )(
                <Select>
                  {listRoom.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group" style={{ 'margin-top': '19px' }}>
              <BtnSearch htmlType="submit" />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



