import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import to from 'await-to-js';
import { Card, Row, Col, Button, Icon, Form } from 'antd';
import { addSubjectByRoomId, deleteSubjectByRoomId } from 'services/roomSubject';

import FormInfoCommon from './FormInfoCommon';
import TableRoomsForAssignSubject from './TableRoomsForAssignSubject';
import TableSubjectNameNotSelected from './TableSubjectNameNotSelected';
import TableSubjectNameSelected from './TableSubjectNameSelected';
import './style.scss';

@Form.create({ name: 'formSearch' })
class ClassRoomsAssignedListPage extends Component {
  state = {
    formSearch: {},
    infoRoom: {},
    listSubject: [],
    listSubjectSelect: [],
    reload: 1,
    resetData: 1,
  };

  getDataForm = valueForm => {
    this.setState({
      formSearch: valueForm,
      resetData: this.state.resetData + 1,
      listSubjectSelect: [],
    });
  };

  dataRoomChoose = value => {
    this.setState({
      infoRoom: value,
    });
  };

  chooseSubject = record => {
    this.setState({
      listSubject: record,
    });
  };

  listChooseSubjectSelect = record => {
    this.setState({
      listSubjectSelect: record,
    });
  };

  addSubject = async () => {
    const { reload, listSubject } = this.state;
    const listSubjectId = listSubject.map(item => {
      return {
        roomId: item.roomId,
        subjectId: item.subjectId,
      };
    });

    const [error] = await to(addSubjectByRoomId(listSubjectId));
    if (error) return;
    this.setState({
      reload: reload + 1,
    });
  };
  deleteSubject = async () => {
    const { reload, listSubjectSelect } = this.state;
    const newSubject = listSubjectSelect.map(item => {
      return {
        roomId: item.roomId,
        subjectId: item.subjectId,
      };
    });
    const [error] = await to(deleteSubjectByRoomId(newSubject));
    if (error) return;
    this.setState({
      reload: reload + 1,
    });
  };

  render() {
    const { infoRoom, reload, listSubject, resetData, listSubjectSelect } = this.state;
    return (
      <Card>
        <Row>
          <Col span={24}>
            <FormInfoCommon formData={this.getDataForm} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableRoomsForAssignSubject
              dataSearch={this.state.formSearch}
              chooseRoom={this.dataRoomChoose}
            />
          </Col>
        </Row>
        <Row>
          <Col span={11}>
            <TableSubjectNameNotSelected
              infoRoom={infoRoom}
              loadData={reload}
              resetData={resetData}
              listChooseSubject={this.chooseSubject}
            />
          </Col>
          <Col span={2}>
            <div className="btnPlusMinus-group">
              <Button
                disabled={!listSubject.length > 0}
                style={{ 'margin-bottom': '5px' }}
                onClick={this.addSubject}
              >
                <Icon type="plus" />
              </Button>
              <Button disabled={!listSubjectSelect.length > 0} onClick={this.deleteSubject}>
                <Icon type="minus" />
              </Button>
            </div>
          </Col>
          <Col span={11}>
            <TableSubjectNameSelected
              infoRoom={infoRoom}
              loadData={reload}
              resetData={resetData}
              listChooseSubjectSelect={this.listChooseSubjectSelect}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassRoomsAssignedListPage />
  </Page>
);



