import React, { Component } from 'react';
import { Divider, Table } from 'antd';
import to from 'await-to-js';
import { getRoomSubjectByRoomId } from 'services/roomSubject';

class TableSubjectNameSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      data: [],
      selectedRowKeys: [],
    };

    this.columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
    ];
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.infoRoom !== this.props.infoRoom || nextProps.loadData !== this.props.loadData) {
      this.getRoomSubject(nextProps.infoRoom);
      this.setState({
        selectedRowKeys: [],
      });
      this.props.listChooseSubjectSelect([]);
    }
    if (nextProps.resetData !== this.props.resetData) {
      this.setState({
        data: [],
      });
    }
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({
      selectedRowKeys,
    });
    this.props.listChooseSubjectSelect(selectedRows);
  };

  getRoomSubject = async (value = {}) => {
    this.setState({ loading: true });
    const roomId = value.roomTypeId;
    const [error, response] = await to(getRoomSubjectByRoomId({ roomId }));
    this.setState({ loading: false });
    if (error) return;
    this.setState({ data: response.data.items });
  };

  render() {
    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <Divider>DS môn học đã chọn</Divider>
        <Table
          bordered
          loading={loading}
          rowSelection={rowSelection}
          dataSource={this.state.data}
          columns={this.columns}
          pagination={false}
        />
      </div>
    );
  }
}

export default TableSubjectNameSelected;



