import { request } from 'utils';

const prefix = '/api/education';

export const getRoomSubjectByRoomId = ({ roomId, isPagingEnabled = false } = {}) =>
  request({
    prefix,
    url: '/Room_Subject',
    method: 'GET',
    params: {
      roomId,
      isPagingEnabled,
    },
    data: {},
  });

export const getSubjectByRoomId = ({ roomId }) =>
  request({
    prefix,
    url: `/Room_Subject/Subjects`,
    params: {
      roomId,
    },
    method: 'GET',
  });

export const addSubjectByRoomId = listSubjectId =>
  request({
    prefix,
    url: `/Room_Subject`,
    data: [...listSubjectId],
    method: 'POST',
  });

export const deleteSubjectByRoomId = listSubjectId =>
  request({
    prefix,
    url: `/Room_Subject`,
    data: [...listSubjectId],
    method: 'DELETE',
  });



